* {
  margin: 0px;
  padding: 0px;
}

.menu {
  display: flex;
  height: 100vh;
  width: 400px;
  background-color: gainsboro;
  float: right;
  flex-direction: column;
}

.numInp {
  width: 378px;
  height: 30px;
  padding: 5px;
  margin: 5px;
  font-family: "Courier New", Courier, monospace;
  font-weight: bold;
  font-size: large;
}

.duckButton {
  width: 390px;
  height: 50px;
  padding: 5px;
  margin: 5px;
  position: relative;
  top: 10%;
  font-family: "Courier New", Courier, monospace;
  font-weight: bold;
}

.credit {
  display: flex;
  align-items: end;
  justify-content: left;
  font-family: "Courier New", Courier, monospace;
  font-size: x-small;
}

.desc {
  display: flex;
  white-space: pre-line;
  padding: 5px;
  margin: 5px;
  font-family: "Courier New", Courier, monospace;
  font-size: medium;
  font-weight: bold;
}
